import { useState } from "react";
import { SlPresent } from "react-icons/sl";

import KTY from "../../img/kty.jpeg";
import Profile from "../../img/profile.jpeg";
import Boom from "../../img/giftBoom.jpg";

export default function GiftDraw() {
    // 버튼 기능 막기
    const [disabledButtons, setDisabledButtons] = useState(false);
    // 팝업
    const [show, setShow] = useState("displaynone");

    // 선물 목록
    const [giftResult, setGiftResult] = useState({
        img: "",
        txt: ""
    });
    const giftArray = [{
        img: Profile,
        txt: "고급 선물 당첨입니다! 잘 부탁드립니다."
    },{
        img: KTY,
        txt: "허허 중급 선물입니다! 좋은 하루 되십쇼!"
    },{
        img: Boom,
        txt: "꽝이지만 잘 부탁드립니다!"
    }];
    
    // 확률
    const percentage = (percent) => {
        const random = percent;
        console.log(random);

        if (random > 0 && random <= 50) {
            return 0;
        } else if (random > 50 && random <= 80) {
            return 1;
        } else if (random > 80 && random <= 100) {
            return 2;
        }
    };
    // 클릭시
    const drawOnClick = () => {
        setDisabledButtons(true);

        setTimeout(() => {
            setGiftResult(giftArray[percentage(Math.floor(Math.random() * 100) + 1)]);
            setShow("");
        }, 100);
    };

    const resetOnClick = () => {
        setDisabledButtons(false);
        setShow("displaynone");
    }
    
    return (
        <>
            <h1 className="game-tit">선물 뽑기</h1>

            <div className="game-giftDraw">
                <p>확률 정보</p>
                <ul>
                    <li>고급 선물 : 50%</li>
                    <li>중급 선물 : 30%</li>
                    <li>꽝 : 20%</li>
                </ul>
                <div className={`result-show ${show}`}>
                    <p>{giftResult.txt}</p>
                    <img src={giftResult.img} alt="선물" />
                    <button 
                        type="button"   
                        className="reset-btn"
                        onClick={() => {resetOnClick()}}
                    >다시 뽑기</button>
                </div>
                <div className="gift-boxes">
                    {
                        giftArray.map((e,idx)=>{
                            return (
                                <button
                                    key={idx}
                                    type="button"
                                    className={`gift gift${idx+1}`}
                                    disabled={disabledButtons}
                                    onClick={() => {
                                        drawOnClick();
                                    }}
                                >
                                    <SlPresent />
                                </button>
                            )
                        })
                    }
                </div>
            </div>
        </>
    )
}