import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

import work1_1 from "../img/work/work1-1.png";
import work1_2 from "../img/work/work1-2.png";
import work1_3 from "../img/work/work1-3.png";

import work3_1 from "../img/work/work3-1.png";
import work3_2 from "../img/work/work3-2.png";

import work4_1 from "../img/work/work4-1.png";
import work4_2 from "../img/work/work4-2.png";
import work4_3 from "../img/work/work4-3.png";

import gsap from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";

gsap.registerPlugin(ScrollToPlugin);

export default function Work() {
    const isMobile = useSelector((state) => state.isMobile);

    // 타이틀 교체
    const [pageTit, setPageTit] = useState("이런 작업을 했습니다.");
    const workList = [
        "Ajax GET/POST 통신",
        "Next.js 자사몰 작업 및 유지 보수",
        "확률형 소비자 참여형 이벤트",
        "특정 시간 자동 변경 스크립트",
        "Cafe24 자사몰 런칭, 리뉴얼, 유지보수"
    ];

    const sec1Ref = useRef(null);
    const sec2Ref = useRef(null);
    const sec3Ref = useRef(null);
    const sec4Ref = useRef(null);
    const sec5Ref = useRef(null);

    useEffect(() => {
        const titChange = () => {
            const sct = window.scrollY || document.documentElement.scrollTop;
            const sec1 = sec1Ref.current.offsetTop - 500;
            const sec2 = sec2Ref.current.offsetTop - 500;
            const sec3 = sec3Ref.current.offsetTop - 500;
            const sec4 = sec4Ref.current.offsetTop - 500;
            const sec5 = sec5Ref.current.offsetTop - 500;

            if (sct < sec1) {
                setPageTit("이런 작업을 했습니다.");
            } else if (sec1 <= sct && sct < sec2) {
                setPageTit(workList[0]);
            } else if (sec2 <= sct && sct < sec3) {
                setPageTit(workList[1]);
            } else if (sec3 <= sct && sct < sec4) {
                setPageTit(workList[2]);
            } else if (sec4 <= sct && sct < sec5) {
                setPageTit(workList[3]);
            } else if (sec5 <= sct) {
                setPageTit(workList[4]);
            }
        }

        window.addEventListener("scroll", titChange);
        return () => {
            window.removeEventListener("scroll", titChange);
        };
    }, []);

    const anchorNav = (e, idx) => {
        e.preventDefault(); 
    
        gsap.to(window, {
            duration: 0.5,
            scrollTo: { y: `#work${idx + 1}`, offsetY: 175 }, 
            ease: "power2.out", 
        });
    };

    return (
        <div className="work-wrap">
            <div className="head"><p>{pageTit}</p><span>Yung's Work</span></div>
            <section className="work-list">
                <ul>
                    {workList.map((item, idx) => (
                        <li key={idx}>
                            <p onClick={(e) => anchorNav(e, idx)}>
                                {item}
                            </p>
                        </li>
                    ))}
                </ul>
            </section>
            <section className="sec sec1" id="work1" ref={sec1Ref}>
                <div className="img-box">
                    <img src={work4_1} />
                    <img src={work4_2} />
                    <img src={work4_3} />
                </div>
                <p className="work-summary">
                    백엔드 팀에서 구독 관련 앱을 만들어 Cafe24 자사몰 페스룸에 적용했습니다.
                    여러 기능 중 구독자 이탈을 방지할 페이지가 필요하게 되어 추가 작업을 맡게 되었습니다.<br/><br/>
                    기존엔 해지하기 버튼을 누르는 즉시 알럿창이 노출되면서 해지됐지만
                    버튼 클릭 시 해지하려는 상품의 정보를 보여주는 STEP 1 페이지,
                    해당 상품의 해지가 구독으로 인해 주어지는 VIP 멤버십 자격이 박탈 당하는 경우에 보여주는 STEP 2 페이지가 추가됐습니다.<br/><br/>
                    STEP 1은 구독 상품 목록 페이지에서 각 상품의 정보를 모아 해지하기 버튼 클릭 시
                    SessionStorage를 사용하여 각 페이지간의 정보를 주고 받아 노출 시켰습니다.
                    STEP 2 페이지에선 백엔드 서버와 GET/POST 통신을 통해
                    프론트에서 member_id, invoice_no, item_code, option_id 정보를 주고
                    총 구매액, 총 할인 받은 금액, 총 누적 마일리지 값을 받아와 노출 시키는 작업을 했습니다.<br/><br/>
                    이때 프론트엔드 개발자의 역할을 조금이라도 수행하고 문제없이 완료할 수 있어 좋은 경험이었습니다.
                </p>
            </section>
            <section className="sec sec2" id="work2" ref={sec2Ref}>
                <div className="img-box">
                    <img src={work3_1} />
                    <img src={work3_2} />
                </div>
                <p className="work-summary">
                    입사 후 1년쯤 Next.js로 구축해둔 자사 플랫폼에서 이벤트 페이지 작업을 할 수 있게 됐습니다.<br/><br/>
                    React.js/Next.js의 경험이 없었고 Cafe24만 사용하는 이곳에서는 쉽게 찾아오지 않는 기회라 생각했습니다.
                    몇 날 며칠을 구글링하고 Chat GPT와 소통하며 이벤트 페이지에 사용되는 기능을 구현할 수 있었습니다.
                    기존의 자바스크립트, 반복되는 html 구조를 훅과 map 함수를 통해 효율적인 코드 작성이 가능한 것을 느꼈습니다.
                    그 뒤로도 이벤트 페이지 작업, 온리드로우&#40;래플&#41; 페이지 작업을 맡았습니다.<br/><br/>
                    맨땅에 헤딩하듯이 접한 Next.js는 코딩애플 React.js 강의를 통해 더 정확한 개념과 사용법을 알게 됐습니다.<br/><br/>
                    이후 IT 개발본부가 꾸려지면서 자사 내에서도 Next.js 구독몰&#40;미우타임즈&#41;이 구축됐습니다.
                    해당 구독몰을 작업하면서 Tailwind도 사용해 보고 여러 IT 직무와 Jira, Git으로 유지 보수 작업도 했습니다.
                </p>
            </section>
            <section className="sec sec3" id="work3" ref={sec3Ref}>
                <div className="img-box">
                    <img src={work1_1} />
                    {!isMobile ? <img src={work1_2} /> : null}
                    <img src={work1_3} />
                </div>
                <p className="work-summary">
                    소비자 참여형 이벤트로 룰렛 돌리기 기능을 구현했습니다.
                    룰렛을 돌려 확률에 따라 고객에게 쿠폰을 주는 게임이였습니다.
                    추가로 1일 참여 횟수 제한이 있는 경우 쿠키를 사용하여 참여 제한하였습니다.<br/><br/>
                    해당 기능은 선물뽑기, 풍선 터트리기 등 다양한 컨셉으로
                    자사 브랜드&#40;페스룸, 위글위글, 온리버디 등&#41; 이벤트에 적용되며
                    이벤트 페이지 작업을 담당하게 되었습니다.<br/><br/>
                    게임이 진행되는 단계, 참여 완료했을 때, 쿠키를 줘야 할 때 등
                    입사 초반에 많은 경우의 수를 직접 생각해서 스크립트를 짜 볼 수 있었던 좋은 경험이였습니다.
                    회사 입장에서는 처음으로 소비자 참여형 이벤트를 열 수 있었고
                    아무 액션 없이 뿌리는 쿠폰의 다운로드 수 보다 약 10배가량 증가하였습니다.
                </p>
                <a href="https://aquamarine-alley-ce1.notion.site/17dca5692e3e80e0adfdd875f3d0bcc5" target="_blank">참여형 이벤트 모음집</a>
            </section>
            <section className="sec sec4" id="work4" ref={sec4Ref}>
                <p className="work-summary">
                    자동 시간 업데이트 스크립트 작업은 저의 자그만한 불만에서 시작되었습니다.
                    앞에서 이야기한 소비자 참여형 이벤트는 주로 00시에 오픈/마감/업데이트가 이뤄졌습니다.
                    띠배너, GNB, 메인페이지 등 사이트 전체를 이벤트 페이지로 많이 유입되게 변경하는 작업이 00시에 진행되었고
                    페이지 내 쿠폰 링크 변경, 타임특가&#40;커밍순/오픈/솔드아웃&#41;와 같이 특정 시간에 변경하는 작업이 필요했습니다.<br/><br/>
                    하지만 Cafe24 특성상 수작업 이후 캐시 삭제를 통해 사이트에 반영 시켜야 했습니다.
                    이는 정확한 시간에 반영이 될 수 있을지 모르며 계속해서 캐시 삭제 버튼을 눌러야 하는 비효율적인 문제가 있었습니다.
                    이 상황의 해결 방법으로 특정 시간 자동 변경 스크립트를 짜보겠다고 팀장님께 말씀드렸습니다.<br/><br/>
                    그렇게 Date&#40;&#41; 기반의 스크립트 코드를 짜게 되었고 수동으로 변경해야했던 모든 작업들을 자동으로 변경되게, 검수만 하면 되도록 바꿨습니다.
                    이후에도 하나의 클래스명 유무로 변경되야 할 모든 부분을 제어할 수 있게 최적화 시켰습니다.
                </p>
            </section>
            <section className="sec sec5" id="work5" ref={sec5Ref}>
                <p className="work-summary">
                    Cafe24는 현재까지 계속 주업무로 작업하고 있습니다.
                    가장 최근엔 <a href="https://hotnthings.com/" target="_blank">하랜띵스</a>라는 브랜드 런칭 작업을 담당했습니다.
                    기본 스킨 바탕으로 모든 페이지 커스텀 및 퍼블리싱 하였으며 다양한 모션을 원하셔서 GSAP-ScrollTrigger 라이브러리도 처음 사용해봤습니다.
                    또 <a href="https://bmsmile.com/" target="_blank">비엠스마일 공식 홈페이지</a>도 모든 페이지 퍼블리싱으로 작업 했습니다.<br/><br/>
                    리뉴얼은 페스룸, 1993스튜디오 브랜드 위주로 페이지 리뉴얼 작업을 담당했고
                    특히 페스룸은 1차 리뉴얼 이후 구독 서비스가 도입되면서 2차 사이트 전체 리뉴얼 작업을 진행했습니다.<br/><br/>
                    이후 각 브랜드들의 오류 수정 또는 코드 최적화를 위해 유지보수 작업도 했습니다.
                    Cafe24 모듈, 변수들을 자유자재로 커스텀하여 작업할 수 있는 경험이였습니다.<br/><br/>
                    이외에도 각 쇼핑몰에 필요할 경우 외부 솔루션 서비스를 찾고 분석하여 쇼핑몰에 도입한 경험이 있습니다.
                    &#40;위글위글 - 기프틴/선물하기 기능 서비스, 비엠스마일 - 그리팅/채용 솔루션&#41;
                </p>
            </section>
        </div>
    )
}