import React, { useEffect, useRef, useState } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

import KTY from "../img/kty.jpeg";
import Profile from "../img/profile.jpeg";


import gsap from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";

gsap.registerPlugin(ScrollToPlugin);

export default function Kty() {
    // 유저 네임
    const userName = useSelector((state) => state.user);

    useEffect(() => {
        AOS.init();
    },[]);

    // 내비, 디졸브 효과
    const mainRef = useRef(null);
    const sec1Ref = useRef(null);
    const sec2Ref = useRef(null);
    const sec3Ref = useRef(null);
    const sec4Ref = useRef(null);
    const [nav, setNav] = useState("");
    const [sec3, setSec3] = useState("");
    const [actSlide, setActSlide] = useState(null); // 현재 활성화된 슬라이드

    useEffect(() => {

        const dissolveAct = () => {
            const sct = window.scrollY || document.documentElement.scrollTop;
            const sec1Top = sec1Ref.current.offsetTop;
            const scrollH = sec1Ref.current.offsetHeight;
            const scrollH2n = scrollH / 4;

            if (sct >= sec1Top - 100 && sct <= scrollH2n * 2) {
                setActSlide(1);
            } else if (sct >= scrollH2n * 2 && sct < scrollH2n * 4) {
                setActSlide(2);
            } else {
                setActSlide(null); // 조건에 맞지 않을 경우 초기화
            }
        };

        const navAct = () => {
            const sctNav = window.scrollY || document.documentElement.scrollTop;
            const nav1 = sec1Ref.current.offsetTop - 500;
            const nav2 = sec2Ref.current.offsetTop - 500;
            const nav3 = sec3Ref.current.offsetTop - 500;
            const nav4 = sec4Ref.current.offsetTop - 500;

            if (sctNav < nav1) {
                setNav("main-nav");
                setSec3("");
            } else if (nav1 <= sctNav && sctNav < nav2) {
                setNav("sec1-nav");
                setSec3("");
            } else if (nav2 <= sctNav && sctNav < nav3) {
                setNav("sec2-nav");
                setSec3("");
            } else if (nav3 <= sctNav && sctNav < nav4) {
                setNav("sec3-nav");
                setSec3("on");
            } else if (nav4 <= sctNav) {
                setNav("no");
                setSec3("");
            }
        }

        window.addEventListener("scroll", dissolveAct);
        window.addEventListener("scroll", navAct);
        return () => {
            window.removeEventListener("scroll", dissolveAct);
            window.removeEventListener("scroll", navAct);
        };
    }, []);

    const [secDisplay, setSecDisplay] = useState("");
    useEffect(() => {
        setTimeout(()=>{setSecDisplay("displayblock")}, 3000);
        return () => {

        }
    }, []);

    const anchorNav = (e, targetId) => {
        e.preventDefault();
    
        gsap.to(window, {
            duration: 0.5, 
            scrollTo: { y: `#${targetId}`, offsetY: 75 }, 
            ease: "power2.out", 
        });
    };


    return (
        <div className="kty-wrap">
            <section className="main-sec" id="ktyMain" ref={mainRef}>
                <div className="inner">
                    <h2 data-aos="fade-zoom-in" data-aos-delay="500" data-aos-duration="1000">어서오세요<br/>{userName}님</h2>
                    <div className="img"><img src={KTY} alt="웃고 있는 캐릭터"/></div>
                </div>
            </section>

            <nav>
                <ul className={`${nav}`}>
                    <li onClick={(e) => anchorNav(e, "ktyMain")}>HELLO</li>
                    <li onClick={(e) => anchorNav(e, "kty1")}>MIND</li>
                    <li onClick={(e) => anchorNav(e, "kty2")}>KTY</li>
                    <li onClick={(e) => anchorNav(e, "kty3")}>CAREER</li>
                </ul>
            </nav>

            <section className={`sec sec1 ${secDisplay}`} id="kty1" ref={sec1Ref}>
                <div className="scroll-zone">
                    <div className={`con con1 ${actSlide === 1 ? "on" : ""}`}>
                        <p><span>후회</span>가 남지 않도록<br/><span>최선</span>을 다하자</p>
                    </div>

                    <div className={`con con2 ${actSlide === 2 ? "on" : ""}`}>
                        <p>하고 싶은 <span>일</span><br/>하면서 <span>살자</span></p>
                    </div>
                </div>
            </section>

            <section className={`sec sec2 ${secDisplay}`} id="kty2" ref={sec2Ref}>
                <div className="inner">
                    <div className="profile">
                        <div className="img-box" data-aos="fade-right" data-aos-duration="1000"><img src={Profile} alt="김태융" className="w100"/></div>
                        <div className="txt-box" data-aos="fade-up" data-aos-duration="1000">
                            <h3>안녕하세요<br/>웹퍼블리셔 김태융입니다.</h3>
                            <p>
                                하고 싶은 일을 하며 살기 위해<br/>후회 없도록 최선을 다하자는 마음가짐으로 살고 있습니다.<br/><br/>
                                퍼블리싱을 좋아하는 이유는 머릿속으로 설계도를 구상하고<br/>그것을 실제로 구현 해내는 과정이 재밌기 때문입니다.<br/>
                                많은 생각을 요구하는 스크립트를 좋아하고<br/>계속해서 효율적인 코드로 최적화 시키는 것을 좋아합니다.<br/><br/>
                                생각하고 코드를 짜며 어떠한 미션이라도 해내겠습니다.<br/>거기서 끝이 아닌 계속해서 버전업 시키는 퍼블리셔가 되겠습니다.
                            </p>
                        </div>
                    </div>
                </div>
            </section>

            <section className={`sec sec3 ${sec3} ${secDisplay}`} id="kty3" ref={sec3Ref}>
                <div className="inner">
                    <div className="career" data-aos="fade-up" data-aos-duration="1000">
                        <h4>Career</h4>
                        <ul>
                            <li>조선호텔앤리조트 <span>&#40;2025.06.02 ~ 재직중&#41;</span></li>
                            <li>라이프사업팀 조선e샵파트 - 웹퍼블리셔 파트너</li>
                        </ul>
                        <ul>
                            <li>비엠스마일 <span>&#40;2022.07.25 ~ 2025.02.28&#41;</span></li>
                            <li>디자인팀 웹디자인파트 - 웹퍼블리셔 사원</li>
                            <li><a href="https://aquamarine-alley-ce1.notion.site/233ca5692e3e81bea752fe133ca126ca" target="_blank">🖋️ 경력 기술서 →</a></li>
                        </ul>
                    </div>
                    <div className="edu" data-aos="fade-up" data-aos-duration="1000">
                        <h4>Education</h4>
                        <ul>
                            <li>SBS아카데미컴퓨터아트학원 - 웹퍼블리셔 과정 <span>&#40;2021. 07 ~ 2022. 04&#41;</span></li>
                            <li>연암공과대학교 스마트소프트웨어학과 3년제 - 2학년 1학기 중퇴 <span>&#40;2018. 03 ~ 2024. 10&#41;</span></li>
                        </ul>
                    </div>

                    <div className="cert" data-aos="fade-up" data-aos-duration="1000">
                        <h4>Certificate</h4>
                        <ul>
                            <li>웹디자인 기능사</li>
                            <li>컴퓨터그래픽스운용 기능사</li>
                            <li>ACA Illustrator</li>
                            <li>ACA Photoshop</li>
                        </ul>
                    </div>
                </div>
            </section>

            <section className={`sec sec4 ${secDisplay}`} id="kty4" ref={sec4Ref}>
                <div className="inner">
                    <p data-aos="fade-zoom-in" data-aos-delay="500" data-aos-duration="1000">
                        즐기면서 만든 포트폴리오<br/>
                        재밌게 보고 가주십쇼
                    </p>
                </div>
            </section>
        </div>
    )
}