import moment from "moment";
import 'moment/locale/ko';
import { useEffect, useState } from "react";


export default function Time() {

    const [currentTime, setCurrentTime] = useState(moment());

    useEffect(() => {
        const updateTime = setInterval(() => {
            setCurrentTime(moment()); // 매 초마다 현재 시간을 업데이트
        }, 1000);

        return () => clearInterval(updateTime); // 컴포넌트 언마운트 시 타이머 정리
    }, []);

    return (
        <>
            <div className="date">{currentTime.format("MMM Do")} {currentTime.format("dddd")}</div>
            <div className="time">{currentTime.format("HH:mm")}</div>
        </>
    )
}