import { useState, useEffect, useRef } from "react";

export default function Quiz() {
    const [answers, setAnswers] = useState(["", "", ""]);
    const inputRefs = useRef([]);
    const correctAnswer = ["김", "태", "융"];
    const [isCorrect, setIsCorrect] = useState(null);

    const handleChange = (index, value) => {
        setIsCorrect(null);
        const newAnswers = [...answers];
        newAnswers[index] = value;
        setAnswers(newAnswers);
    };

    const handleKeyDown = (index, e) => {
        if (e.key === "Backspace" && !answers[index] && index > 0) {
            inputRefs.current[index - 1].focus();
        }
    };

    const checkAnswer = () => {
        if (answers.join("") === correctAnswer.join("")) {
            setIsCorrect(true);
        } else {
            setIsCorrect(false);
            setAnswers(["", "", ""]);
            inputRefs.current[0].focus();
        }
    };

    return (
        <>
            <h1 className="game-tit">퀴즈 맞추기</h1>

            <div className="game-word">
                <div className="quiz-Q">
                    <p>
                        "하고 싶은 일 하면서 살자. 대신 후회가 없게 최선을 다하자" 라는
                        마인드로 살아가는 인물은 누구인가?
                    </p>
                    <span>Hint. 김타융? 김톼유? 김융태? 김태융?</span>
                </div>

                <div className="quiz-A">
                    {correctAnswer.map((el, idx) => (
                        <input
                            key={idx}
                            type="text"
                            maxLength="1"
                            value={answers[idx]}
                            onChange={(e) => handleChange(idx, e.target.value)}
                            onKeyDown={(e) => handleKeyDown(idx, e)}
                            ref={(el) => (inputRefs.current[idx] = el)}
                            className={`A ${isCorrect === true ? 'yes-A' : isCorrect === false ? 'no-A' : ''}`}
                        />
                    ))}
                </div>

                <div className="A-btn" onClick={checkAnswer}>정답</div>

                {
                    isCorrect === true 
                    ? <div className="result-text">정답입니다!</div>
                    : isCorrect === false 
                    ? <div className="result-text error">틀렸습니다!</div>
                    : null
                }
            </div>
        </>
    );
}
